import {uploadImage} from './cloudStorage'






async function subirProducto(){
    const imgUrl= await uploadImage()
	//const imgUrl = await getUrlFromFirebaseImg(metadata)
    let form = $('#productoForm')
    let formData =  form.serialize()
    //console.log(imgMetaData)
    console.log(imgUrl)
    formData+='&img=' + imgUrl
    $.ajax({
        type: 'post',
        url: '/producto/store',
        data: formData,
        success: function (data) {
            console.log(data)
        },
        error: function (data) {
          console.log(data)
      }
    });
}
async function getUrlFromFirebaseImg(imgMetadato){
	//const nombreImg = imgMetaData.metadata.name
	console.log(imgMetadato)
	const bucket = imgMetadato.metadata.bucket
	const imgPath = imgMetadato.ref._location.path.replaceAll('/', '\%2F')
	const providerHost = imgMetadato.ref._service._host
	const imgUrl = providerHost + '/v0/b/' + bucket + '/o/' + imgPath + '?alt=media'
	return imgUrl
}


function previewFile() {
    var file  = document.querySelector('input[type=file]').files[0];
    var preview = document.querySelector('#imgPreview');
   
    var reader  = new FileReader();
  
    reader.onloadend = function () {
      preview.src = reader.result;
    }
  
    if (file) {
      reader.readAsDataURL(file);
    } else {
      preview.src = "/images/default-placeholder.png";
    }
  }


  $("#productSubmit").on('click', subirProducto)
$("#inputimg").on('change', previewFile)